/* Footer CSS */

li {
  border-right: 1px solid #bbb;
}

li:last-child {
  border-right: none;
}

 ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}


li a:hover {
  background-color: #4CAF50;
}

.active {
  background-color: #4CAF50;
  float: right;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%
}